/**
 * PuzzleGenerator - Slider verification for bot protection
 */

/**
 * Generates a unique verification token for slider puzzle
 */
export function generateVerificationToken(): string {
  const timestamp = Date.now();
  const random = Math.random().toString(36).substring(2, 15);
  return `${timestamp}-${random}`;
}

/**
 * Validates the slider puzzle completion
 * Checks if the token format is valid and recent (within 5 minutes)
 */
export function validateSliderCompletion(token: string): boolean {
  try {
    if (!token || typeof token !== 'string') {
      console.log("ðŸ”´ Token validation failed: Token is missing or not a string");
      return false;
    }

    // Token format: timestamp-randomstring
    const parts = token.split('-');
    if (parts.length !== 2) {
      console.log("ðŸ”´ Token validation failed: Invalid token format (expected timestamp-random)");
      return false;
    }

    const timestamp = parseInt(parts[0], 10);
    if (isNaN(timestamp)) {
      console.log("ðŸ”´ Token validation failed: Invalid timestamp");
      return false;
    }

    // Check if token is recent (within 5 minutes)
    const now = Date.now();
    const fiveMinutes = 5 * 60 * 1000;
    const age = now - timestamp;
    
    if (age > fiveMinutes) {
      console.log(`ðŸ”´ Token validation failed: Token expired (age: ${Math.round(age / 1000)}s, max: 300s)`);
      return false;
    }

    console.log(`ðŸŸ¢ Token validated successfully (age: ${Math.round(age / 1000)}s)`);
    return true;
  } catch (error) {
    console.log("ðŸ”´ Token validation failed with error:", error);
    return false;
  }
}

