"use client";

import React, { useState, useRef, useEffect } from "react";
import { X } from "lucide-react";

interface SliderPuzzleProps {
  isOpen: boolean;
  onClose: () => void;
  onSuccess: () => void;
}

const SliderPuzzle: React.FC<SliderPuzzleProps> = ({ isOpen, onClose, onSuccess }) => {
  const [sliderPosition, setSliderPosition] = useState(0);
  const [isDragging, setIsDragging] = useState(false);
  const [isVerified, setIsVerified] = useState(false);
  const [hasCalledSuccess, setHasCalledSuccess] = useState(false);
  const sliderRef = useRef<HTMLDivElement>(null);
  const trackRef = useRef<HTMLDivElement>(null);

  const THRESHOLD = 95; // 95% completion to verify

  useEffect(() => {
    if (isVerified && !hasCalledSuccess) {
      setHasCalledSuccess(true);
      // Small delay before calling success
      const timer = setTimeout(() => {
        onSuccess();
      }, 500);
      return () => clearTimeout(timer);
    }
  }, [isVerified, hasCalledSuccess, onSuccess]);

  // Reset when modal opens/closes
  useEffect(() => {
    if (!isOpen) {
      setSliderPosition(0);
      setIsVerified(false);
      setHasCalledSuccess(false);
      setIsDragging(false);
    }
  }, [isOpen]);

  const handleMouseDown = () => {
    setIsDragging(true);
  };

  const handleMouseUp = () => {
    setIsDragging(false);
    if (sliderPosition < THRESHOLD) {
      // Reset if not completed
      setSliderPosition(0);
    }
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isDragging || !trackRef.current) return;

    const track = trackRef.current;
    const rect = track.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const percentage = (x / rect.width) * 100;

    const clampedPercentage = Math.max(0, Math.min(100, percentage));
    setSliderPosition(clampedPercentage);

    if (clampedPercentage >= THRESHOLD && !isVerified) {
      setIsVerified(true);
    }
  };

  const handleTouchStart = () => {
    setIsDragging(true);
  };

  const handleTouchEnd = () => {
    setIsDragging(false);
    if (sliderPosition < THRESHOLD) {
      setSliderPosition(0);
    }
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    if (!isDragging || !trackRef.current) return;

    const track = trackRef.current;
    const rect = track.getBoundingClientRect();
    const touch = e.touches[0];
    const x = touch.clientX - rect.left;
    const percentage = (x / rect.width) * 100;

    const clampedPercentage = Math.max(0, Math.min(100, percentage));
    setSliderPosition(clampedPercentage);

    if (clampedPercentage >= THRESHOLD && !isVerified) {
      setIsVerified(true);
    }
  };

  if (!isOpen) return null;

  return (
    <div
      className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50 backdrop-blur-sm"
      onMouseMove={handleMouseMove}
      onMouseUp={handleMouseUp}
    >
      <div className="bg-white rounded-2xl shadow-2xl p-8 max-w-md w-full mx-4 relative animate-in fade-in zoom-in duration-300">
        {/* Close button */}
        <button
          onClick={onClose}
          className="absolute top-4 right-4 text-gray-400 hover:text-gray-600 transition-colors"
          disabled={isVerified}
        >
          <X size={24} />
        </button>

        {/* Header */}
        <div className="text-center mb-6">
          <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <svg
              className="w-8 h-8 text-blue-600"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z"
              />
            </svg>
          </div>
          <h3 className="text-2xl font-bold text-gray-800 mb-2">Verify You're Human</h3>
          <p className="text-gray-600">
            {isVerified ? "Verification complete!" : "Slide to verify and continue"}
          </p>
        </div>

        {/* Slider Track */}
        <div
          ref={trackRef}
          className={`relative w-full h-16 rounded-full overflow-hidden ${
            isVerified ? "bg-green-100" : "bg-gray-100"
          } transition-colors duration-300`}
        >
          {/* Progress Fill */}
          <div
            className={`absolute top-0 left-0 h-full ${
              isVerified ? "bg-green-500" : "bg-blue-500"
            } transition-all duration-200`}
            style={{ width: `${sliderPosition}%` }}
          />

          {/* Slider Button */}
          <div
            ref={sliderRef}
            className={`absolute top-1 left-1 w-14 h-14 rounded-full cursor-grab active:cursor-grabbing flex items-center justify-center shadow-lg transition-all duration-200 ${
              isVerified
                ? "bg-green-600"
                : isDragging
                ? "bg-blue-600 scale-110"
                : "bg-white"
            }`}
            style={{
              left: `calc(${sliderPosition}% - 28px)`,
              maxWidth: "calc(100% - 8px)",
            }}
            onMouseDown={handleMouseDown}
            onTouchStart={handleTouchStart}
            onTouchMove={handleTouchMove}
            onTouchEnd={handleTouchEnd}
          >
            {isVerified ? (
              <svg
                className="w-8 h-8 text-white"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={3}
                  d="M5 13l4 4L19 7"
                />
              </svg>
            ) : (
              <svg
                className={`w-6 h-6 ${isDragging ? "text-white" : "text-blue-600"}`}
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M13 7l5 5m0 0l-5 5m5-5H6"
                />
              </svg>
            )}
          </div>

          {/* Text */}
          <div className="absolute inset-0 flex items-center justify-center pointer-events-none">
            <span
              className={`text-sm font-semibold ${
                isVerified ? "text-green-700" : "text-gray-500"
              }`}
            >
              {isVerified ? "âœ“ Verified" : "Slide to verify"}
            </span>
          </div>
        </div>

        {/* Progress indicator */}
        {!isVerified && (
          <p className="text-center text-xs text-gray-500 mt-4">
            {sliderPosition > 0 && sliderPosition < THRESHOLD
              ? `${Math.round(sliderPosition)}% complete`
              : "Drag the slider all the way to the right"}
          </p>
        )}
      </div>
    </div>
  );
};

export default SliderPuzzle;

